export function contentProcessor(array) {
  let name = Object.keys(array)[0]

  let newArr = {}
  newArr[name] = []

  const recurse = (arr, keys) => {
    keys = keys || name

    _.forEach(arr, function(el, k) { 
      if ((typeof el === 'object' ||Array.isArray(el)) && el !== null) {
        if (k == "image" && el.image && el.image.image) {
          _.set(newArr, `${keys}.${k}`, el.image.image)
        } else if (k == 'children') {
          recurse(el, `${keys}`)
        } else {
          recurse(el, `${keys}.${k}`)
        }
      } else {
        if (k == "en") {
          _.set(newArr, `${keys}`, el)
        } else if (k !== "id" && k !== "query_id") {
          _.set(newArr, `${keys}.${k}`, el)
        }
      }
    });
  }

  recurse(_.get(array, name))

  return newArr
}

export function removeFocusOutline() {
  document.addEventListener('mousedown', function() {
    var el = document.getElementById('outline-none')
    console.log('mousel', el)
    
    if (el == null) {
      var css = '* { outline: none !important; }';

      var style = document.createElement('style');
      style.id = 'outline-none';
      style.appendChild(document.createTextNode(css));

      document.body.appendChild(style);
    }
  });

  document.addEventListener('keydown', function() {
    var el = document.getElementById('outline-none')
    console.log('el', el)
    
    if (el != null) {
      el.remove();
    }
  });
}
