import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import { Slide, Stagger } from '@/components/Animation';
import contentMatrix from '@/data/contentMatrix';
import { loader } from '@/plugins/components/Preloader';
import { ATTRACT_HAND } from '@/config/config';

export default function Content({ handleClick }) {
  const [isAnim, setIsAnim] = useState(true);
  useEffect(() => {
    const timeout = setTimeout(
      () => {
        setIsAnim(!isAnim);
      },
      isAnim ? 14000 : 1000
    );
    return () => {
      clearTimeout(timeout);
    };
  }, [isAnim]);

  return (
    <Stagger
      delay={1000}
      animate={isAnim}
      animationComponent={Slide}
      content={[
        <Title>{contentMatrix.attract.title}</Title>,
        <CTA onClick={handleClick} tabIndex={-1}>
          {contentMatrix.attract.buttonLabel}
          <CTAImage src={loader.get(ATTRACT_HAND).src} />
        </CTA>,
      ]}
    />
  );
}

const Title = styled.h1`
  padding-bottom: ${vw(29)};
    font-size: ${vw(110)};
    text-transform: capitalize;
    letter-spacing: ${vw(2.6)};
    line-height: ${vw(110)};
    text-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    font-family: ${({ theme }) => theme.font.chakra};
    font-weight: 500;
    font-style: normal;
`;

const CTA = styled.button`
  position: relative;
  top: ${vw(20)};
  left: ${vw(0)};
  padding-top: ${vw(20)};
  padding-bottom: ${vw(20)};
  padding-left: ${vw(60)};
  font-size: ${vw(22)};
  font-family: ${({ theme }) => theme.font.industry};
  font-weight: 500;
  font-style: normal;
  letter-spacing: ${vw(1.37)};
  color: ${({ theme }) => theme.color.tertiary};
`;

const CTAImage = styled.img`
  position: absolute;
  top: 0%;
  left: 0%;
  width: ${vw(45)};
  height: ${vw(64)};
  animation: pulse 4s infinite;
  @keyframes pulse {
    0% {
      transform: scale(0.8);
    }

    70% {
      transform: scale(1);
    }

    100% {
      transform: scale(0.8);
    }
  }
`;
