import { getUrls } from "@/redux/config";
import { fetchJson } from "@/utils/fetch";

// Types
export const TEST__GET_DATA = "TEST::GET_DATA";
export const TEST__SET_DATA = "TEST::SET_DATA";

// Actions
export const getData = () => (dispatch) => ({
  type: TEST__GET_DATA,
});

export const fetchData = () => async (dispatch, getStore) => {
  const store = getStore();
  const urls = getUrls(store);
  const contentUrl = urls.contentUrl;
  await fetchJson(contentUrl).then((data) => {
    dispatch({
      type: TEST__SET_DATA,
      payload: { data: data.testUrls },
    });
  });
};

// Initial State
const initialState = {
  data: [],
};

// Reducer
const finalFoo = (state = initialState, action = {}) => {
  switch (action.type) {
    case TEST__SET_DATA:
      return {
        ...state,
        data: action.payload.data,
      };
    default:
      return state;
  }
};

// Selectors

export const getUrlsToPreload = (state) => {
  // return state.test.data;
}

export default finalFoo;
