import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';

import store from '@/redux/store';
import reducers from '@/redux/reducers';
import { setIsKeypadAudio } from '@/redux/app';

const initialState = reducers();
const defaultConfig = {
  shouldReset: false,
  keycode: {
    inserted: 45,
    removed: 46,
  },
};

/**
 * Detects whether headphone is plugged in the EZ keypad.
 */
export default function useKeypadAudioDetect(config) {
  const { shouldReset, keycode } = { ...defaultConfig, config };
  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    const reset = isKeypadAudio => {
      store.dispatch({
        type: 'RESET',
        state: {
          ...initialState,
          app: { ...initialState.app, isKeypadAudio },
        },
      });
      history.push('/');
    };

    const handleKeyUp = ({ which }) => {
      const { inserted, removed } = keycode;
      switch (which) {
        case inserted:
          shouldReset ? reset(true) : dispatch(setIsKeypadAudio(true));
          break;
        case removed:
          shouldReset ? reset(false) : dispatch(setIsKeypadAudio(false));
          break;
        default:
          return;
      }
    };

    document.addEventListener('keyup', handleKeyUp);
    return () => {
      document.removeEventListener('keyup', handleKeyUp);
    };
  }, [history, dispatch, keycode, shouldReset]);
}
