import React from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import arrow360Image from '@/assets/images/icons/360-arrow-active.svg';
import arrow360ImageActive from '@/assets/images/icons/360-arrow.svg';
import contentMatrix from '@/data/contentMatrix';
import { Slide } from '@/components/Animation';

export default function ArrowControls({
  active,
  handleArrows,
  handleFocus,
  arrowsContainerRef,
  arrowPressedIndex,
  isArrowControlActive,
  tabIndex,
})
{

  const onArrowClick =(index)=>{
    handleArrows(index);
  }
  return (
    <Slide direction="up" value={100}>
    <Root 
      active={active }
      ref={arrowsContainerRef}
      onFocus={handleFocus}
      // onBlur={handleFocus}
      tabIndex={tabIndex}
      aria-roledescription=" "
      aria-label={(arrowPressedIndex!==null && arrowPressedIndex!==5) ?
        `${arrowPressedIndex+1} of ${contentMatrix.main.ArrowGroup.length}, ${contentMatrix.main.ArrowGroup[arrowPressedIndex].ad} button ` 
        :(!isArrowControlActive ? contentMatrix.main.tabGroup.arrowControlGroupADNotActive : arrowPressedIndex===null ? contentMatrix.main.tabGroup.arrowControlGroupADActive : '')}
    >
 
        <ThreeSixtyIcon src={require('@/assets/images/icons/360.svg')} />
        
        {contentMatrix.main.ArrowGroup.map((arrow, index) => (
            <Arrow
            key={index}
            src={arrowPressedIndex===index ? arrow360Image : arrow360ImageActive}
            style={{ top: vw(arrow.top), left: vw(arrow.left) }}
            rotate={arrow.rotate}
            onClick={()=>onArrowClick(arrow.onClickIndex)}
            tabIndex={-1}
            active={arrowPressedIndex===arrow.onClickIndex}
            />
      ))}
    
    </Root>
    </Slide>
  );
}

const Root = styled.div`
  position: absolute;
  width: ${vw(212)};
  height: ${vw(212)};
  top: ${vw(850)};
  left: ${vw(1336)};
/* background: green; */
  opacity: ${(props) => (props.active ? 0 : 1)};
  transition: all 0.33s;
`;


const ThreeSixtyIcon = styled.img`
  position: absolute;
  width: ${vw(79)};
  height: ${vw(79)};
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
`;

const Arrow = styled.button`
  position: absolute;
  width: ${vw(51)};
  height: ${vw(51)};
  transform: rotate(${(props) => props.rotate}deg);
  background-image: url(${props=>props.src});
  background-size: contain;
  background-position:center;
  :active{
    background-image: url(${arrow360Image});
  }
`;