import * as THREE from 'three';

export default class Marker extends THREE.Object3D {
  constructor() {
    super();
    this.name = 'marker';
    const material = new THREE.MeshPhongMaterial({
      color: 0xffffff,
      opacity: 0,
      transparent: true,
    });
    const cylinder = new THREE.Mesh(
      new THREE.SphereGeometry(0.25, 50, 50),
      material
    );
    this.add(cylinder);
  }
}
