import React, { Component } from 'react';
import * as THREE from 'three';
import styled from 'styled-components';
import CameraControls from 'camera-controls';
import initializeDomEvents from 'threex-domevents';
import Planet from './objects/Planet';
import Hotspot from './elements/Hotspot';
import { loader } from '@/plugins/components/Preloader';
import contentMatrix from '@/data/contentMatrix';
const CAMERA_POSITION = 15;

let THREEx = {};

CameraControls.install({ THREE: THREE });
initializeDomEvents(THREE, THREEx);

export default class Model3D extends Component {
  state = {
    hotspots: [],
  };

  constructor(props) {
    super(props);
    this.$canvas = React.createRef();
    this.requestAnimationIdRef = React.createRef();
    this.clock = new THREE.Clock();
    this.renderer = null;
    this.camera = null;
    this.cameraControls = null;
    this.scene = null;
    this.markers = [];
    this.tempV = new THREE.Vector3();
    this.raycaster = new THREE.Raycaster();
  }

  componentDidMount() {
    this.renderer = new THREE.WebGLRenderer({
      canvas: this.$canvas.current,
      alpha: true,
      antialias: true,
    });

    this.scene = new THREE.Scene();

    this.initCamera();
    this.initLighting();

    this.domEvents = new THREEx.DomEvents(
      this.camera,
      this.renderer.domElement
    );

    if (this.props.planet) {
      this.initPlanet();
    }

    if (this.props.models) {
      this.props.models.forEach(this.initModel);
    }
    requestAnimationFrame(() => {
      this.renderer.render(this.scene, this.camera);
    });
    this.animate();

    //fixes the intensity/saturation problem for the model
    this.renderer.outputEncoding = THREE.sRGBEncoding;
  }

  handleClickAndRotate = (marker, index) => {
    this.rotateCamera(marker.userData.loc[0], marker.userData.loc[1]);
    this.props.onClickHotspot(index);
  };

  componentWillUnmount() {
    cancelAnimationFrame(this.requestAnimationIdRef.current);

    this.scene.traverse((o) => {
      if (o.geometry) {
        o.geometry.dispose();
        //console.log("dispose geometry ", o.geometry)
      }

      if (o.material) {
        if (o.material.length) {
          for (let i = 0; i < o.material.length; ++i) {
            o.material[i].dispose();
            //console.log("dispose material ", o.material[i])
          }
        } else {
          o.material.dispose();
          //console.log("dispose material ", o.material)
        }
      }
    });

    this.scene = null;
    this.camera = null;
    this.renderer.renderLists.dispose();
    this.renderer.dispose();
    this.$canvas.current = null;
    //console.log('dispose')
  }

  initCamera = () => {
    const fov = 50;
    const aspect = 1;
    const near = 1;
    const far = 100;

    this.camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
    this.camera.position.z = CAMERA_POSITION;

    this.cameraControls = new CameraControls(
      this.camera,
      this.renderer.domElement
    );

    //fixed two finger tap drag and right click drag, they both simply rotat now
    this.cameraControls.mouseButtons = {
      left: CameraControls.ACTION.TOUCH_ROTATE,
      middle: CameraControls.ACTION.TOUCH_ROTATE,
      right: CameraControls.ACTION.TOUCH_ROTATE,
      wheel: CameraControls.ACTION.TOUCH_ROTATE,
    };
    this.cameraControls.touches = {
      one: CameraControls.ACTION.TOUCH_ROTATE,
      two: CameraControls.ACTION.TOUCH_ROTATE,
      three: CameraControls.ACTION.TOUCH_ROTATE,
    };

    // fixed dolly
    this.cameraControls.maxDistance = CAMERA_POSITION;
    this.cameraControls.minDistance = CAMERA_POSITION;
    this.scene.add(this.camera);
  };

  initLighting() {
    let light;
    // ambient lighting for planets
    if (this.props.planet) {
      light = new THREE.AmbientLight(0xffffff);
      light.position.set(100, 100, 100);
      this.camera.add(light);
      this.scene.add(light);
      return;
    }
    // directional lighting for 3D models
    // light2 = new THREE.PointLight(0xffffff, 3);
    // light.position.set(100, 100, 100);

    // LOWERING EXPOSURE
    light = new THREE.AmbientLight('hsl(220, 100%, 100%)', 2);

    this.camera.add(light);
  }

  initPlanet = () => {
    const { radius, texture, hotspots } = this.props.planet;
    const planet = new Planet(radius, texture);
    // render hotspots
    hotspots.forEach((hotspot) => {
      planet.createHotspot(
        {
          lat: hotspot.loc[0],
          lon: hotspot.loc[1],
          radius,
          name: hotspot.name,
        },
        this.scene
      );
    });
    this.planet = planet;
    this.scene.add(planet);

    this.domEvents.addEventListener(planet, 'click', this.handleClickMarker);

    this.markers = this.scene.children.filter(
      (child) => child.name === 'marker'
    );
  };

  initModel = (model) => {
    this.model = loader.get(contentMatrix.main.url);
    this.model.scale.set(
      model.options.scale,
      model.options.scale,
      model.options.scale
    );
    this.scene.add(this.model);

    this.props.hotspots.forEach((hotspot) => {
      this.model.createHotspot(
        {
          x: hotspot.x,
          y: hotspot.y,
          z: hotspot.z,
          loc: hotspot.loc,
          name: hotspot.name,
        },
        this.scene
      );
    });

    this.markers = this.scene.children.filter(
      (child) => child.name === 'marker'
    );
  };

  resize = () => {
    let width = this.renderer.domElement.clientWidth;
    let height = this.renderer.domElement.clientHeight;
    this.renderer.setSize(width, height, false);
    this.camera.aspect = width / height;
    this.camera.updateProjectionMatrix();
  };

  updateLabels = () => {
    const hotspots = this.markers.map((marker, index) => {
      // get the position of the center of the cube
      marker.updateWorldMatrix(true, false);
      marker.getWorldPosition(this.tempV);
      // get the normalized screen coordinate of that position
      // x and y will be in the -1 to +1 range with x = -1 being
      // on the left and y = -1 being on the bottom
      this.tempV.project(this.camera);

      // ask the raycaster for all the objects that intersect
      // from the eye toward this object's position
      this.raycaster.setFromCamera(this.tempV, this.camera);
      const intersectedObjects = this.raycaster.intersectObjects(
        this.scene.children,
        true
      );

      const show =
        intersectedObjects.length &&
        marker === intersectedObjects[0].object.parent;
      // convert the normalized position to CSS coordinates
      const x =
        (this.tempV.x * 0.5 + 0.5) *
        (this.$canvas.current == null ? 0 : this.$canvas.current.clientWidth);
      const y =
        (this.tempV.y * -0.5 + 0.5) *
        (this.$canvas.current == null ? 0 : this.$canvas.current.clientHeight);
      return {
        position: { x, y },
        show,
      };
    });

    this.setState({ hotspots });
  };

  rotateCamera = (lat, lon, verticalOffset, horizontalOffset) => {
    let leftTilt = horizontalOffset ? horizontalOffset : 0;
    let topTilt = verticalOffset ? verticalOffset : 0;
    let polarAngle = ((90 - lat + topTilt) * Math.PI) / 180;
    let azimuthAngle = ((lon + 70 + leftTilt) * Math.PI) / 180;
    // console.log('val ', offset);
    this.cameraControls.normalizeRotations();
    this.cameraControls.rotateTo(azimuthAngle, polarAngle, true);
  };

  handleClickMarker = (e) => {
    if (e.intersect.object.parent.name !== 'marker') return;
    let marker = e.intersect.object.parent;
    const { loc } = marker.userData;
    const offset = marker.verticalOffset ? marker.verticalOffset : 0;
    this.rotateCamera(loc[0], loc[1], offset);
  };

  animate = () => {
    const delta = this.clock.getDelta();
    const hasControlsUpdated = this.cameraControls.update(delta);
    // if (hasControlsUpdated) {
    this.renderer.render(this.scene, this.camera);
    // }

    this.updateLabels();
    this.resize();
    this.requestAnimationId = requestAnimationFrame(this.animate);
  };

  render() {
    return (
      <Root>
        <Canvas ref={this.$canvas} onMouseMove={this.handleMouseMove} />
        <Hotspots active={true}>
          {this.markers.map((marker, index) => (
            <Hotspot
              key={index}
              position={
                this.state.hotspots[index] === undefined
                  ? 0
                  : this.state.hotspots[index].position
              }
              show={
                this.state.hotspots[index] === undefined
                  ? 0
                  : this.state.hotspots[index].show
              }
              name={marker.userData.name}
              onClick={() => this.handleClickAndRotate(marker, index)}
              index={index}
              hotspotNums={this.markers.length}
              active={this.props.activeHotspotIndex === index}
              hide={this.props.activeHotspotIndex === null}
              tooltipRef={
                index === this.props.hotspotFocusedIndex
                  ? this.props.tooltipRef
                  : null
              }
              handleKeyDownForHotspotFocus={
                this.props.handleKeyDownForHotspotFocus
              }
            />
          ))}
        </Hotspots>
      </Root>
    );
  }
}

const Root = styled.div`
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: 100%;
  background: pink;
`;

const Canvas = styled.canvas`
  position: absolute;
  left: 0;

  top: 0%;
  width: 100%;
  height: 100%;
  background: black;
`;

const Hotspots = styled.div`
  opacity: ${(props) => (props.active ? 1 : 0)};
  position: absolute;
  top: 0;
  left: 0;
  transition: opacity 1.5s ease-in-out;
`;
