import LoaderBase from 'preloader/lib/loaders/LoaderBase';
import Model from '@/plugins/components/preloader/loaderModel/Model';

export default class LoaderModel extends LoaderBase {
  constructor(options) {
    super('Custom', options);
  }

  load = (file) => {
    const onProgress = (progressEvent) => {
      const progress = progressEvent.loaded / progressEvent.total;
      if (progress >= 1) {
        this._dispatchComplete();
      } else {
        this._onProgress(progressEvent, true);
      }
    };
    const test = new Model(file, onProgress);
    this.content = test;
  };
}
