import React from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import useResetHook from '@/plugins/hooks/useResetHook';
import contentMatrix from '@/data/contentMatrix';
import { Slide } from '@/components/Animation';
import DivWithRefControl from '@/plugins/components/DivWithRefControl';


export default function StartOverButton({
  tabIndex,
  zindex,
  startOverRef,
  nextItemRef,
  sideTabRef,
}) {
  const reset = useResetHook();

  return (
    <Slide direction="up" value={100}>
      <Root  
        onClick={reset}
        handleKeyDown={reset}  
        tabIndex={tabIndex}
        zindex={zindex}
        ref={startOverRef}
        prevTabRef={sideTabRef}
        nextTabRef={nextItemRef}
        aria-roledescription={`button, ${contentMatrix.main.pressAD}`}
      >
        <span>Start Over</span>
      </Root>
    </Slide>
  );
}

const Root = styled(DivWithRefControl)`
  z-index: ${props=>props.zindex===null ? '' : props.zindex};
  position: absolute;
  width: ${vw(180)};
  height: ${vw(52)};
  left: ${vw(35)};
  bottom: ${vw(35)};
  font-size: ${vw(22)};
  font-family: ${({ theme }) => theme.font.chakra};
  font-weight: 600;
  font-style: normal;
  color: ${({ theme }) => theme.color.tertiary};
  background-image: url(${require('@/assets/images/icons/startover.svg')});
  background-repeat: no-repeat;
  background-size: ${vw(50)} ${vw(52)};
  span {
    margin-left: ${vw(63)};
  }
`;
