import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import styled, { keyframes } from 'styled-components';
import { Pannellum } from 'pannellum-react';
import Hotspot from './elements/Hotspot';
import PropTypes from 'prop-types';
import { loader } from '@/plugins/components/Preloader';
export default function Panorama360({
  settings,
  image,
  hotspots,
  activeHotspotIndex,
  onClickHotspot,
  show,
  activeYaw,
  activePitch,
  tabIndex,
  hotspotFocusedIndex,
  handleKeyDownForHotspotFocus,
  tooltipRef,
}) {
  const [hotspotTransformStyles, setHotspotTransformStyles] = useState(null);
  

  const handleClickHotspot = (index) => {
    onClickHotspot(index);
  };

  const handlePanoramaRender = () => {
    const $pnlmHotspots = document.querySelectorAll('.pnlm-hotspot-base');
    setHotspotTransformStyles(
      Array.from($pnlmHotspots).map(($el) => {
        return $el.style.transform;
      })
    );
  };

  return (
    <Root show={show}>
      <Pannellum
        doubleClickZoom={false}
        mouseZoom={false}
        keyboardZoom={false}
        showControls={false}
        width="100%"
        height="100%"
        minHfov={100}
        maxHfov={100}
        {...settings}
        image={loader.get(image).src}
        pitch={activePitch}
        yaw={activeYaw}
        hotspotDebug={false}
        autoLoad
        onRender={handlePanoramaRender}
      >
        {hotspots.map((hotspot, index) => (
          <Pannellum.Hotspot
            key={`${index}-${activeHotspotIndex}`}
            type="custom"
            pitch={hotspot.location[0]}
            yaw={hotspot.location[1]}
            tooltip={(el) => {
              ReactDOM.render(<div></div>, el);
            }}
          ></Pannellum.Hotspot>
        ))}
      </Pannellum>
      {/* <Overlay /> */}

      {hotspotTransformStyles &&
        hotspots.map((hotspot, index) => (
          <Hotspot
            index={index}
            hotspotNums={hotspots.length}
            key={index}
            active={activeHotspotIndex === index}
            style={hotspotTransformStyles[index]}
            onClick={() => handleClickHotspot(index)}
            hotspot={hotspot}
            activeHotspotIndex={activeHotspotIndex}
            show={activeHotspotIndex === null || activeHotspotIndex === index}
            tooltipRef={index===hotspotFocusedIndex ? tooltipRef : null}
            hotspotFocusedIndex={hotspotFocusedIndex}
            handleKeyDownForHotspotFocus={handleKeyDownForHotspotFocus}            
          />
        ))}
    </Root>
  );
}

Panorama360.propTypes = {
  settings: PropTypes.object,
  hotspots: PropTypes.array,
  hotspotStartTabIndex: PropTypes.number,
  activeHotspotIndex: PropTypes.number,
  onClickHotspot: PropTypes.func,
};

Panorama360.defaultProps = {
  settings: {},
  hotspots: [],
  hotspotStartTabIndex: null,
  activeHotspotIndex: null,
  onClickHotspot: null,
};

const animationEnter = keyframes`
  from { opacity: 0;} 
  to { opacity: 1; }
`;

const Root = styled.div`
  display: ${(props) => (props.show ? 'block' : 'none')};
  background: #000;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  animation: ${animationEnter} 1s forwards;
  .pnlm-container {
    background: transparent;
  }
  .pnlm-load-box {
    display: none !important;
  }
  .pnlm-about-msg {
    display: none !important;
  }

  .pnlm-grab {
    z-index: 999;
  }
  .pnlm-grabbing{
    z-index: 999;

  }
  .pnlm-sprite{
    z-index: 999;

  }
  .pnlm-dragfix,
.pnlm-preview-img {
  position: absolute;
  z-index: 999;

}
`;
