import React, { useState, useCallback, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { vw } from '@/styles/utils';
import { loader } from '@/plugins/components/Preloader';
// import { CLOSE_ICON } from '@/config/config';

const SHOW_MODAL_ANIMATION_DURATION = 500;

export default function Modal({
  className,
  visible,
  onClose,
  extra,
  closeButtonTabIndex,
  closeButtonAriaLabel,
  closeButtonAriaRoledescription,
  children,
}) {
  const [fullyVisible, setFullyVisible] = useState(visible);

  useEffect(() => {
    const timeout = setTimeout(
      () => {
        setFullyVisible(visible);
      },
      visible ? 0 : 500
    );
    return () => {
      clearTimeout(timeout);
    };
  }, [visible]);

  const stopPropagation = useCallback((e) => {
    e.stopPropagation();
  }, []);

  if (!visible && !fullyVisible) {
    return null;
  }

  return (
    <Root visible={visible} fullyVisible={fullyVisible} onClick={onClose}>
      <Wrapper
        className={className}
        fullyVisible={fullyVisible}
        onClick={stopPropagation}
        visible={visible}
      >
        {children}
        <CloseButton
          tabIndex={closeButtonTabIndex}
          aria-label={closeButtonAriaLabel}
          aria-roledescription={closeButtonAriaRoledescription}
          onClick={onClose}
        />
      </Wrapper>
      {extra}
    </Root>
  );
}

const showOverlayAnimation = keyframes`
  0% { 
    opacity: 0;
  }
  40% { 
    opacity: 1; 
  }
  100% { 
    opacity: 1; 
  }
`;

const hideOverlayAnimation = keyframes`
  0% { 
    opacity: 1;
  }
  60% { 
    opacity: 1; 
  }
  100% { 
    opacity: 0; 
  }
`;

const Root = styled.div`
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  right: 0;
  backdrop-filter: blur(1px);
  background-color: rgba(216, 216, 216, 0.1);
  z-index: 900;
  animation: ${({ visible }) =>
      visible ? showOverlayAnimation : hideOverlayAnimation}
    ${SHOW_MODAL_ANIMATION_DURATION}ms forwards;
`;

const showModalAnimation = keyframes`
  from { 
    opacity: 0;
    // transform: translate(-50%, -50%) scale(0.75) ;
  }
  to { 
    opacity: 1; 
    // transform: translate(-50%, -50%) scale(1);
  }
`;

const hideModalAnimation = keyframes`
  from { 
    opacity: 1; 
    // transform: translate(-50%, -50%) scale(1);
  }
  to { 
    opacity: 0;
    // transform: translate(-50%, -50%) scale(0.75) ;
  }
`;

const Wrapper = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: ${({ visible }) =>
      visible ? showModalAnimation : hideModalAnimation}
    ${SHOW_MODAL_ANIMATION_DURATION}ms forwards;
`;

const CloseButton = styled.button`
  width: ${vw(48)};
  height: ${vw(48)};
  position: absolute;
  top: ${vw(24)};
  right: ${vw(24)};
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  z-index: 1000;
`;
