import { useEffect, useState } from 'react';
import { isTimeoutPopupActiveSelector } from '@/redux/app';
import { useSelector } from 'react-redux';

const UP = 38;
const DOWN = 40;
const TAB = 9;
const SHIFT = 16;
const ENTER = 13;


export default function useArrowControls(
  isControlFocused,
  setControlFocused,
  isArrowControlActive,
  setArrowControlActive,
  handleArrows,
  arrowsContainerRef,
  setArrowPressedIndex,
       ) {
        const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);
const [isShiftActive, setisShiftActive] =useState(false);
        
        //LISTENS TO KEYS AND MATCHES TO ARROW NAVIGATION
        useEffect(() => {
          if(isTimeoutPopupActive) {
            setArrowControlActive(false);
            setControlFocused(false);
          }
          else{
            const handleKeyUp = (e) => {
              const which =e.keyCode;
              if (isControlFocused===false) return;
              if(which===ENTER && isControlFocused){
                setArrowControlActive(!isArrowControlActive);
                if(!isArrowControlActive){
                  setArrowPressedIndex(null);
                }
                arrowsContainerRef.current.focus();
              }
              

              if(isArrowControlActive){
                setArrowPressedIndex(5);
               
                console.log("which",which);
                if(which===UP){
                  
                  handleArrows(2);
                }

                if(which===DOWN){
                 
                  handleArrows(-2);
                }
                if(which===SHIFT){
                  setisShiftActive(true);
                  handleArrows(-1);
                  setisShiftActive(false);
                  // let interval = setInterval(()=>{
                   
                  //   setisShiftActive(false);
                    
                  //   clearInterval(interval);
                  // },500);
                }
                
                if(which===TAB){
                  console.log("isShiftActive from Tab",  isShiftActive);
                  if (isShiftActive){
                    console.log("from Tab");
                    handleArrows(-1);
                  }   
                  else{
                    
                    console.log("from else Tab"); 
                    handleArrows(1);
                  }
                }
                // arrowsContainerRef.current.focus();
              }
            
            };
            const handlekeyDown = (e) => {
              const which = e.keyCode;
              if (isControlFocused===false) return;
              switch (which) {
                case UP:
                  if(isArrowControlActive){
                    setArrowPressedIndex(0);
                    e.preventDefault();
                    arrowsContainerRef.current.focus();
                  }
                  break;
                case DOWN:
                  if(isArrowControlActive){
                    setArrowPressedIndex(1)
                    e.preventDefault();
                    arrowsContainerRef.current.focus();
                  }
                  break;
      
                case TAB:
                  if(isArrowControlActive){
                    if(e.shiftKey){
                        setArrowPressedIndex(2);
                        e.preventDefault();
                        arrowsContainerRef.current.focus();
                      }
                      else{
                        setArrowPressedIndex(3);
                        e.preventDefault();
                        arrowsContainerRef.current.focus();
                      }
                  }
                  

                  break;  
                      
                default:
                  return;
              }
            };
            document.addEventListener('keyup', handleKeyUp);
            document.addEventListener('keydown', handlekeyDown);

            return () => {
              document.removeEventListener('keyup', handleKeyUp);
              document.removeEventListener('keydown', handlekeyDown);
            };
          }
          }, [isTimeoutPopupActive, isControlFocused,setControlFocused, isArrowControlActive, setArrowControlActive, handleArrows, setArrowPressedIndex, arrowsContainerRef, isShiftActive]);


      
    

  }

  //test e.prevent default(); to see if it fixes the switch    