import * as THREE from 'three';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader';
import Marker from '@/plugins/components/preloader/loaderModel/Marker';

export default class Model extends THREE.Object3D {
  constructor(url, onProgress) {
    super();
    const fbxLoader = new FBXLoader();

    fbxLoader.load(
      url,
      (root) => {
        this.add(root);
      },
      onProgress
    );
  }

  createHotspot = (options, scene) => {
    const { x, y, z, loc, name } = options;
    const marker = new Marker();

    marker.position.set(x, y, z);
    marker.userData = { loc, name };
    scene.add(marker);
  };
}
