import React from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import contentMatrix from '@/data/contentMatrix';
import PropTypes from 'prop-types';
import { Slide } from '@/components/Animation';
import { loader } from '@/plugins/components/Preloader';
import { SHUTTLE_IMAGE } from '@/config/config';
import KeypadVerticalNavList from '@/plugins/components/KeypadVerticalNavList';

export default function Sidebar({
  tabIndex,
  views,
  activeViewIndex,
  onClickTab,
  isActiveHotspot,
  handleArrowBoxBlur,
  arrowsContainerRef,
  sideTabRef,
}) {

  return (
    <Slide direction="left" value={100}>
    <Root isActiveHotspot={isActiveHotspot}> 
      <h1>
        {contentMatrix.main.sidebarTitle}
      </h1>

      <Shuttle
        src={loader.get(SHUTTLE_IMAGE).src}
        alt={contentMatrix.main.shuttleImageAlt}    
      />

      <Tabs 
        prevTabRef={arrowsContainerRef}
        ref={sideTabRef} 
        tabIndex={tabIndex}
        onFocus={handleArrowBoxBlur}
        aria-roledescription={' '}
        aria-label={`${contentMatrix.main.tabGroup.sidebarGroupAD}`}
      >
        {views.map((view, index) => (
          <Tab
            key={index}     
            aria-roledescription ={`${index+1} of ${views.length},${view.title} Button,
             ${activeViewIndex === index ? 'selected' : ''}`}
            aria-label={' '}
            tabIndex={tabIndex}
            data-testid={`tab-${index}`}
            active={activeViewIndex === index}
            onClick={() => onClickTab(index)}
          >
            <span>{view.title}</span>
          </Tab>
        ))}
      </Tabs>
      <h4>
        {contentMatrix.main.visitLinkText}
      </h4>
    </Root>
    </Slide>
  );
}

Sidebar.propTypes = {
  views: PropTypes.array,
  activeViewIndex: PropTypes.number,
  onClickTab: PropTypes.func,
  isActiveHotspot: PropTypes.bool,
  indexTab: PropTypes.number,
};

Sidebar.defaultProps = {
  views: [],
  activeViewIndex: null,
  onClickTab: null,
  isActiveHotspot: false,
  indexTab: 1,
};

const Root = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  right: 0;
  width: ${vw(340)};
  height: 100%;
  background: ${({ theme }) => theme.color.secondary};
  padding: ${vw(35)};
  h1 {
    position: relative;
    top: ${vw(40)};
    margin-left: auto;
    margin-right: auto;
    text-transform: uppercase;
    height: ${vw(210)};
    width: ${vw(228)};
    color: ${({ theme }) => theme.color.tertiary};
    font-size: ${vw(38)};
    letter-spacing: ${vw(2)};
    line-height: ${vw(47)};
    font-weight: 500;
    span{
      font-size: ${vw(24)};
      line-height:${vw(1.26)};
    }
 
  }
  h4{
    position: absolute;
    bottom: ${vw(45)};
    margin-left: auto;
    margin-right: auto;
    height: ${vw(95)};
    width: ${vw(284)};
    color: ${({ theme }) => theme.color.tertiary};
    font-family: ${({ theme }) => theme.font.nunito};
    font-size: ${vw(20)};
    line-height: ${vw(24)};
    font-weight: 500;
    opacity: 0.8;
  }
`;

const Tabs = styled(KeypadVerticalNavList)`
  position: absolute;
  z-index:997;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: ${vw(520)};
  bottom: ${vw(20)};
  width: ${vw(300)};
  height: ${vw(1050)};

`;

const Tab = styled.button`
  position: relative;
  width: ${vw(283)};
  height: ${vw(89)};
  display: block;
  text-align: center;
  border: ${vw(3)} solid ${({ theme }) => theme.color.primary};
  border-radius: ${vw(12)};
  background-color: ${(props) =>
    props.active ? props.theme.color.primary : props.theme.color.secondary};
  margin-top: ${vw(25)};
  font-family: ${({ theme }) => theme.font.chakra};
  font-size: ${vw(22)};
  font-weight: 600;
  font-style: normal;
  line-height: 1.27;
  span {
    color: ${(props) =>
      props.active ? props.theme.color.secondary : props.theme.color.tertiary};
  }
  :active{
      background: #B98A00;
    }
`;
const Shuttle = styled.img`
  position: absolute;
  height: ${vw(195)};
  width: ${vw(284)};
  top: ${vw(297)};
  right: auto;
`;
