import React from 'react';
import styled, { keyframes } from 'styled-components';
import { loader } from "@/plugins/components/Preloader";
import { GRAPH } from '@/config/config';
import { vw } from '@/styles/utils';

export default function Graph(props) {
  return <Root {...props} />;
}

const animateGraph = keyframes`
  100% {
    background-position: ${vw(-241)} 0;
  }
`;

const Root = styled.div`
  position: absolute;
  width: ${vw(241)};
  height: ${vw(72)};
  background-image: url(${loader.get(GRAPH).src});
  background-size: 100%;
  animation: ${animateGraph} 10s linear infinite;
  opacity: 0.7;
`;
