import React from 'react';
import styled, { keyframes } from 'styled-components';

import { vw } from '@/styles/utils';

export default function Plus(props) {
  return (
    <Root {...props}>
      <span />
      <span />
    </Root>
  );
}

const animateDot = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;

const Root = styled.div`
  position: absolute;
  span {
    margin: 0 ${vw(2)};
    width: ${vw(40)};
    height: ${vw(40)};
    display: inline-block;
    background-image: url(${require('@/assets/images/icons/plus.svg')});
    background-repeat:no-repeat;
    background-size:${vw(40)} ${vw(40)};
    animation: ${animateDot} 1s ease-in-out infinite alternate-reverse;
    &:nth-child(1) {
      animation-duration: 1s;
      animation-delay: 1s;
    }
    &:nth-child(2) {
      animation-duration: 1.1s;
      animation-delay: 1.3s;
    }
\
  }
`;
