import * as THREE from 'three';
import Marker from './Marker';

export default function Planet(radius, texture) {
  this.userData = { radius };

  THREE.Object3D.call(this);

  let geometry = new THREE.SphereGeometry(radius, 100, 100);
  let material = new THREE.MeshPhongMaterial();
  material.map = new THREE.TextureLoader().load(texture);

  let mesh = new THREE.Mesh(geometry, material);
  this.add(mesh);
}

Planet.prototype = Object.create(THREE.Object3D.prototype);

Planet.prototype.createHotspot = function(options, scene) {
  const { lat, lon, radius, name } = options;
  const marker = new Marker();
  const lonRad = -lon * (Math.PI / 180);
  const latRad = lat * (Math.PI / 180);

  const position = {
    x: Math.cos(latRad) * Math.cos(lonRad) * radius,
    y: Math.sin(latRad) * radius,
    z: Math.cos(latRad) * Math.sin(lonRad) * radius,
  };
  marker.position.set(position.x, position.y, position.z);
  marker.userData = { loc: [lat, lon], name };
  marker.rotation.set(0.0, -lonRad, latRad - Math.PI * 0.5);
  scene.add(marker);
};
