import React, { useState, useEffect } from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';

export default function Fade(props) {
  const [isAnimate, setIsAnimate] = useState(false);
  const { animate } = props;

  useEffect(() => {
    setTimeout(() => {
      setIsAnimate(animate);
    }, 0);
  }, [animate]);

  return <Root {...props} animate={isAnimate} />;
}

Fade.propTypes = {
  delay: PropTypes.number,
  duration: PropTypes.number,
  animate: PropTypes.bool,
  useKeyframes: PropTypes.bool,
};

Fade.defaultProps = {
  delay: 0,
  duration: 1000,
  animate: true,
  useKeyframes: false,
};

const cssTransition = css`
  opacity: ${props => (props.animate ? 1 : 0)};
  transition: opacity ${props => props.duration}ms;
  transition-delay: ${props => (props.animate ? props.delay : 0)}ms;
`;

const Root = styled.div`
  transform: translateZ(0);
  ${cssTransition}
`;
