import _ from 'lodash';
import model from '@/data/contentMatrix';
import contentMatrix from '@/data/contentMatrix';
const slideImages = contentMatrix.attract.slideImages;

export const ATTRACT_BG1 = slideImages[0];
export const ATTRACT_BG2 = slideImages[1];
export const NO_SOUND = require('@/assets/images/icons/no-sound.png');
export const MAIN_BG = require('@/assets/images/main/bg.jpg');
export const KEYPAD = require('@/assets/images/attract/keypad.PNG');
export const ATTRACT_HAND = require('@/assets/images/icons/touch.svg');
export const GRAPH = require('@/assets/images/attract/graph.svg');
export const CREDIT_ICON = require('@/assets/images/icons/credits-btn.png');
export const STARTOVER_ICON = require('@/assets/images/icons/startover.svg');
export const SELECTION_BUTTON = require('@/assets/images/icons/oval.svg');
export const TAB_ARROW_LEFT = require('@/assets/images/icons/arrow-previous.svg');
export const TAB_ARROW_RIGHT = require('@/assets/images/icons/arrow-next.svg');
export const TAB_TOGGLE_ICON = require('@/assets/images/icons/left.svg');
export const SHUTTLE_IMAGE = require('@/assets/images/view/shuttle.png');
export const HOTSPOT_ACTIVE_BG = require('@/assets/images/view/hotspotW.svg');
export const HOTSPOT_BG = require('@/assets/images/view/hotspotB.svg');
export const HOTSPOT_ICON = require(`@/assets/images/icons/hotspot-plus.svg`);
export const HOTSPOT_ICON_ACTIVE = require(`@/assets/images/icons/hotspot-minus.svg`);
export const TRANSPARENT_CLOSE_ICON = require('@/assets/images/icons/close-button.svg');
const getContentMatrixAssets = () => {
  const views = contentMatrix.main.views;
  const images = _.reduce(
    views,
    (result, view) => {
      const newImages = [];
      newImages.push(view.thumb);
      newImages.push(view.panorama.image);
      return _.concat(result, newImages);
    },
    []
  );
  const hotspotImages = _.reduce(
    views,
    (result, view) => {
      const imgs = _.map(view.hotspots, (hotspot) => {
        return hotspot.src;
      });
      return _.concat(result, imgs);
    },
    []
  );
  return _.concat(images, hotspotImages);
};

const assets = [
  ATTRACT_BG1,
  ATTRACT_BG2,
  KEYPAD,
  MAIN_BG,
  ATTRACT_HAND,
  NO_SOUND,
  GRAPH,
  CREDIT_ICON,
  TRANSPARENT_CLOSE_ICON,
  STARTOVER_ICON,
  SELECTION_BUTTON,
  TAB_ARROW_RIGHT,
  TAB_ARROW_LEFT,
  TAB_TOGGLE_ICON,
  SHUTTLE_IMAGE,
  HOTSPOT_ICON_ACTIVE,
  HOTSPOT_ICON,
  HOTSPOT_BG,
  HOTSPOT_ACTIVE_BG,
];

const output = {
  resetOnIdle: process.env.NODE_ENV === 'production',
  idleTime: 1 * 60 * 1000,
  hasPublicConfigFile: false,
  hasDynamicContent: false,
  preload: _.concat(assets, getContentMatrixAssets()),
  preloadModels: [model.main],
};

export default output;
