import { useEffect, useState } from 'react';

const LEFT = 37;
const RIGHT = 39;


export default function useLeftRightArrowTabbing(parentRef) 
    {
        const [activeElementIndex, setActiveElement] = useState(0);
        const [elementListLength, setElementList]= useState();

        useEffect(() => {
              //get children elements
              if (parentRef && parentRef.current) {
              const refChildren = Object.values(parentRef.current.children);
              //find children with active tabIndex
              const childrenWithActiveTabIndex = refChildren.filter(
                child => (child.tabIndex!==-1)
              );
              setElementList(childrenWithActiveTabIndex.length);
              childrenWithActiveTabIndex[activeElementIndex].focus();
              }
            const handleKeyDown = (e) => {
              const which = e.keyCode;
              switch(which){
                case LEFT:
                  if(activeElementIndex===0){
                    setActiveElement(elementListLength-1);
                  }
                  else{
                    setActiveElement(activeElementIndex-1);
                  }
                  break;
                case RIGHT:
                  if(activeElementIndex===elementListLength-1){
                    setActiveElement(0);
                  }
                  else{
                    setActiveElement(activeElementIndex+1);
                  }
                  break;
                default:
                  return;
              }
              
            };

            document.addEventListener('keydown', handleKeyDown);
            return () => {
              document.removeEventListener('keydown', handleKeyDown);
            };
          },[activeElementIndex, elementListLength, parentRef])
        


    }
