models.forEach((model) => {
  model.hotspots.forEach((hotspot) => {
    let { x, y, z } = hotspot;
    hotspot.loc = getLatitudeLongitude(x, y, z);
  });
 
});

function getLatitudeLongitude(x, y, z) {
  const normalizeLongitude90 = z < 0 ? 90 : -90;
  const longitude = normalizeLongitude90 + (Math.atan(x / z) * 180) / Math.PI;
  const latitude =
    (Math.atan(y / Math.sqrt(Math.pow(x, 2) + Math.pow(z, 2))) * 180) / Math.PI;

  return [latitude, longitude];
}


