import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import Page from '@/components/Page';
import Attract from '../Attract/Attract';
import Main from '../Main/Main';
import View from '../Views/View';
import Modal from '@/components/Modal';
import { loader } from '@/plugins/components/Preloader';
import contentMatrix from '@/data/contentMatrix';

import { activeScreenIndexSelector } from '@/redux/screens';

export default function AppContainer() {
  const attractImage = loader.get(contentMatrix.attract.slideImages[0]).src;

  const activeScreenIndex = useSelector(activeScreenIndexSelector);

  return (
    <Root data-testid="appContainer">
      <ModelContainer zindex={activeScreenIndex === 2 ? 999 : -1}>
        <View />
      </ModelContainer>
      {activeScreenIndex !== 2 && <BgCover src={attractImage} />}

      <Modal visible={activeScreenIndex === 1}>
        <Main />
      </Modal>
      <Modal visible={activeScreenIndex === 0}>
        <Attract />
      </Modal>
    </Root>
  );
}

const Root = styled.div`
  background-size: cover;
  width: 100vw;
  height: 100vh;
`;
const ModelContainer = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: ${(props) => props.zindex};
`;

const BgCover = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url(${(props) => props.src});
`;
