import React from 'react';
import styled, { keyframes } from 'styled-components';

import { vw } from '@/styles/utils';

export default function Waves(props) {
  const imgColor = props.type ? `-${props.type}` : '';
  return (
    <Root
      {...props}
      src={require(`@/assets/images/attract/waves${imgColor}.png`)}
    />
  );
}

const animateWaves = keyframes`

  100% {
    background-position: ${vw(-150)} 0;
  }
`;

const Root = styled.div`
  position: absolute;
  width: ${vw(150)};
  height: ${vw(76)};
  background-image: url(${(props) => props.src});
  background-size: 100%;
  animation: ${animateWaves} 5s linear infinite;
`;
