import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { vw } from '@/styles/utils';
import Tooltip from './Tooltip';
import { loader } from '@/plugins/components/Preloader';
import { HOTSPOT_ICON_ACTIVE, HOTSPOT_ICON } from '@/config/config';

export default function Hotspot({
  active,
  index,
  hotspotNums, 
  style,
  onClick,
  hotspot,
  activeHotspotIndex,
  show,
  hotspotFocusedIndex,
  handleKeyDownForHotspotFocus,
  tooltipRef,
  
}) {


  
  const handleClickHotspot = () => {
    onClick();
  };
  return (
    <Content activeHotspotIndex={activeHotspotIndex} show={show} >
      <Root style={{ transform: `${style} ` }}>
    
        <Tooltip
          active={active}
          show={show}
          forwardRef={tooltipRef}
          hotspot={hotspot}
          tabIndex={-1}
          handleClick={handleClickHotspot}
          hotspotFocusedIndex={hotspotFocusedIndex}
          handlekeyDown={handleKeyDownForHotspotFocus}
          aria-label={`${index+1} of ${hotspotNums}, ${hotspot.title}`}
          aria-roledescription={' '}
          >
          <h2>{hotspot.title}</h2>
        </Tooltip>

        <Button
          active={active}
          src={active ? loader.get(HOTSPOT_ICON_ACTIVE).src : loader.get(HOTSPOT_ICON).src}/>
      </Root>
    </Content>
  );
}

Hotspot.propTypes = {
  onClick: PropTypes.func,
  style: PropTypes.string,
  show: PropTypes.bool,
  active: PropTypes.bool,
  tabIndex: PropTypes.number,
  activeHotspotIndex: PropTypes.number,
  hotspot: PropTypes.object,
};

Hotspot.defaultProps = {
  onClick: null,
  style: '',
  show: false,
  active: false,
  tabIndex: 1,
  activeHotspotIndex: null,
  hotspot: {},
};

const Content = styled.div`
  transition: all 0.33s;
`;

const Root = styled.div`
  border-radius: 50%;
  position: absolute;
  top: 0;
  z-index: ${(props) => (props.active ? 1000 : 0)};
  transform: translate(-50%, -50%);
`;


const Button = styled.div`
  position: relative;
  width: ${vw(74)};
  height: ${vw(74)};
  display: block;
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
  z-index: 3000;
  outline: none;
  pointer-events: none;
  transform: scale(1);
  transition: all 0.5s;
  background-image: url(${props=>props.src});
  background-size: contain;

`;
