import React, { useState, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import StartOverButton from '@/components/StartOverButton';
import { vw } from '@/styles/utils';
import Page from '@/components/Page';
import AudioDescription from '@/components/AudioDescription';
import { setActiveView } from '@/redux/views';
import { sendViewToGA } from '@/redux/gaEvents';
import contentMatrix from '@/data/contentMatrix';
import KeypadVerticalNavList from '@/plugins/components/KeypadVerticalNavList';
import { Slide } from '@/components/Animation';
import { loader } from '@/plugins/components/Preloader';
import { tabable } from '@/utils/screenreader';
import { isTimeoutPopupActiveSelector } from '@/redux/app';
import { MAIN_BG, SELECTION_BUTTON } from '@/config/config';
import {
  setActiveScreenIndex,
  activeScreenIndexSelector,
} from '@/redux/screens';
import { activeViewIndexSelector } from '@/redux/views';

const views = contentMatrix.main.views;
export default function Main() {
  const dispatch = useDispatch();
  const activeViewIndex = useSelector(activeViewIndexSelector);
  const activeScreenIndex = useSelector(activeScreenIndexSelector);
  const isActiveScreen = activeScreenIndex === 1;
  const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);
  const parentRef = useRef();

  const handlekeyDown = (e) => {
    if (activeViewIndex !== null) {
      if (e.key === 'Enter') {
        handleClickButton(activeViewIndex);
      }
    }
  };

  const handleOnFocusOnView = (index) => {
    dispatch(setActiveView(index));
    dispatch(sendViewToGA());
  };

  const [clicked, setClicked] = useState(false);

  const handleClickButton = (index) => {
    if (!clicked) {
      setClicked(true);
      dispatch(setActiveScreenIndex(2));
      dispatch(setActiveView(index));
    }
  };

  return (
    <Root src={loader.get(MAIN_BG).src}>
      <Wrapper ref={parentRef}>
        <Overlay src={require('@/assets/images/main/overlay.png')} />
        <Slide direction="down" value={100}>
          <Content
            tabIndex={tabable(2, !isTimeoutPopupActive && isActiveScreen)}
            aria-label={`${contentMatrix.main.titleBodyAD}, ${contentMatrix.main.visitLinkText}`}
            aria-roledescription=" "
          >
            <h1>{contentMatrix.main.title}</h1>
            <h2>{contentMatrix.main.bodyText}</h2>
          </Content>
        </Slide>
        <List
          tabIndex={tabable(4, !isTimeoutPopupActive && isActiveScreen)}
          aria-label={`${contentMatrix.main.groupAD}, ${contentMatrix.main.pressAD}`}
          aria-roledescription=" "
        >
          {views.map((view, index) => (
            <ButtonDiv
              tabIndex={tabable(5, !isTimeoutPopupActive && isActiveScreen)}
              onClick={() => handleClickButton(index)}
              onKeyDown={handlekeyDown}
              onFocus={() => handleOnFocusOnView(index)}
              aria-label={`${index + 1} of ${views.length}, ${view.title}, ${
                view.main_alt
              }`}
              aria-roledescription=" "
              key={index}
            >
              <Slide delay={600}>
                <ButtonContainer>
                  <Button
                    data-testid={`button-${index}`}
                    index={index}
                    view={view}
                    src={loader.get(view.thumb).src}
                    tabIndex="-1"
                  >
                    <Gradient />
                    <h1>{view.title}</h1>
                    <Oval
                      src={loader.get(SELECTION_BUTTON).src}
                      index={index}
                    />
                  </Button>
                </ButtonContainer>
              </Slide>
            </ButtonDiv>
          ))}
        </List>
        <Slide delay={1000}>
          <VisitText tabIndex="-1">
            {contentMatrix.main.visitLinkText}
          </VisitText>
        </Slide>

        <AudioDescription
          description={contentMatrix.main.audioDescription}
          autoFocus={true}
          zindex={'999'}
        />
        <StartOverButton
          tabIndex={tabable(8, !isTimeoutPopupActive && isActiveScreen)}
        />
      </Wrapper>
    </Root>
  );
}

