import React, { useState, useRef, useCallback, useEffect } from 'react';
import styled from 'styled-components';
import { useSelector, useDispatch } from 'react-redux';
import StartOverButton from '@/components/StartOverButton';
import { setActiveView, setIsSidebarUsed } from '@/redux/views';
import { sendViewToGA, sendHotspotToGA } from '@/redux/gaEvents';

import { setActiveHotspotIndex } from '@/redux/hotspots';
import { vw } from '@/styles/utils';
import Page from '@/components/Page';
import Panorama360 from '@/components/Panorama360/Panorama360';
import Sidebar from './elements/Sidebar';
import HotspotTab from './elements/HotspotTab';
import AudioDescription from '@/components/AudioDescription';
import models from '@/data/models';
import Model3D from '@/components/Model3D/Model3D';
import ArrowControls from '@/components/ArrowControls';
import contentMatrix from '@/data/contentMatrix';
import { tabable } from '@/utils/screenreader';
import { isTimeoutPopupActiveSelector } from '@/redux/app';
import useArrowControls from '@/plugins/hooks/useArrowControls';
import useLeftRightArrowTabbing from '@/plugins/hooks/useLeftRightArrowTabbing';
import { wasKeypadUsed as wasKeypadUsedSelector } from '@/redux/app';
import {
  setActiveScreenIndex,
  activeScreenIndexSelector,
} from '@/redux/screens';

const views = contentMatrix.main.views;
const UP = 38;
const DOWN = 40;
const TAB = 9;
const ENTER = 13;

export default function View(props) {
  const dispatch = useDispatch();
  const activeView = useSelector((state) => state.views.activeView);
  const activeViewIndex = useSelector((state) => state.views.activeViewIndex);
  const activeScreenIndex = useSelector(activeScreenIndexSelector);
  const isActiveScreen = activeScreenIndex === 2;

  const [tabToggle, setTabToggle] = useState(false);

  //coordinates of panorama hotspots
  const [activePitch, setActivePtch] = useState(
    views[activeViewIndex].panorama.pitch
  );
  const [activeYaw, setActiveYaw] = useState(
    views[activeViewIndex].panorama.yaw
  );
  //coordinates of 3D model hotspots
  const [activeLon, setActiveLon] = useState(models[0].defaultLon);
  const [activeLat, setActiveLat] = useState(models[0].defaultLat);
  const wasKeypadUsed = useSelector(wasKeypadUsedSelector);

  const [audioPlayed, setAudioPlayed] = useState(false);
  const activeHotspotIndex = useSelector(
    (state) => state.hotspots.activeHotspotIndex
  );
  const [prevHotspotIndex, setPrevHotspotIndex] = useState(null);
  const isTimeoutPopupActive = useSelector(isTimeoutPopupActiveSelector);
  const [isHotspotGroupADFocusActive, setIsHotspotGroupADFocusActive] =
    useState(false);
  const [hotspotFocusedIndex, setHotspotFocusedIndex] = useState(null);
  const hotspotStartTabIndex = 10;
  const hotspotTabContentGroupRef = useRef();
  const parentRef = useRef();
  const hotspotGroupRef = useRef();
  const hotspotTabCloseButtonRef = useRef();
  const arrowsContainerRef = useRef();
  const startOverRef = useRef();
  const tooltipRef = useRef();
  const sideTabRef = useRef();

  //arrow Control states
  const [isControlFocused, setControlFocused] = useState(false);
  const [isArrowControlActive, setArrowControlActive] = useState(false);
  const [arrowPressedIndex, setArrowPressedIndex] = useState(null);

  const handleAudioDescriptionBlur = useCallback(() => {
    setAudioPlayed(true);
  }, [setAudioPlayed]);

  //Timeout AD autofocus
  useEffect(() => {
    if (!isTimeoutPopupActive) {
      if (tabToggle) {
        hotspotTabContentGroupRef.current.focus();
      } else {
        setAudioPlayed(false);
      }
    }
  }, [tabToggle, isTimeoutPopupActive]);

  useEffect(() => {
    setTabToggle(true);
    dispatch(setIsSidebarUsed(true));
  }, [activeViewIndex, dispatch]);

  // useEffect(() => {
  //   handleClickTab(activeViewIndex);

  // },[]);

  //hotspotTab arrow button functions
  const handleClickNext = () => {
    if (activeHotspotIndex === activeView.hotspots.length - 1) {
      handleClickHotspot(0);
    } else {
      handleClickHotspot(activeHotspotIndex + 1);
      // dispatch(setActiveHotspotIndex(activeHotspotIndex+1));
    }
  };

  const handleClickPrevious = () => {
    if (activeHotspotIndex === 0) {
      handleClickHotspot(activeView.hotspots.length - 1);
    } else {
      handleClickHotspot(activeHotspotIndex - 1);
    }
  };

  const $model3d = useRef();
  const model = models[0];

  const handleClickTab = (index) => {
    setAudioPlayed(false);
    dispatch(setActiveView(index));
    dispatch(sendViewToGA());
    if (prevHotspotIndex !== null) {
      setPrevHotspotIndex(null);
    }
    dispatch(setActiveHotspotIndex(null));
    setHotspotFocusedIndex(null);
    setTabToggle(true);
    if (index > 0) {
      setActivePtch(views[index].panorama.pitch);
      setActiveYaw(views[index].panorama.yaw);
    } else {
      $model3d.current.rotateCamera(models[0].defaultLon, models[0].defaultLat);
    }
  };

  const handleClickHotspot = useCallback(
    (index) => {
      if (activeHotspotIndex === index) {
        setTabToggle(false);
        dispatch(setActiveHotspotIndex(null));
      } else {
        dispatch(setActiveHotspotIndex(index));
        dispatch(sendHotspotToGA());
        setTabToggle(true);
        hotspotTabContentGroupRef.current.focus();
      }

      setPrevHotspotIndex(index);

      if (activeViewIndex > 0) {
        setActivePtch(activeView.hotspots[index].location[0]);
        setActiveYaw(activeView.hotspots[index].location[1] - 10);
      } else {
        setActiveLon(model.hotspots[index].loc[0]);
        setActiveLat(model.hotspots[index].loc[1]);
        const verticalOffset = model.hotspots[index].verticalOffset
          ? model.hotspots[index].verticalOffset
          : 0;
        const horizontalOffset = model.hotspots[index].horizontalOffset
          ? model.hotspots[index].horizontalOffset
          : 0;
        $model3d.current.rotateCamera(
          model.hotspots[index].loc[0],
          model.hotspots[index].loc[1],
          verticalOffset,
          horizontalOffset
        );
      }

      if (activeHotspotIndex === null && !tabToggle) {
        hotspotTabContentGroupRef.current.focus();
      }
    },
    [
      setTabToggle,
      setActiveYaw,
      setActiveLat,
      setActiveLon,
      hotspotTabContentGroupRef,
      activeHotspotIndex,
      activeViewIndex,
      activeView,
      dispatch,
      model,
      tabToggle,
    ]
  );

  //Function that checks what arrow button is clicked
  const handleArrows = useCallback(
    (index) => {
      //3D model controls
      const rotateCameraHelper = (latOrLon, step) => {
        const pos = latOrLon ? activeLat : activeLon; // True lat, false lon
        let newPos = pos + step;

        if (latOrLon) {
          if (newPos <= -80) {
            newPos = -70;
          } else if (newPos >= 120) {
            newPos = 110;
          }
        } else {
          if (newPos <= -80) {
            newPos = -70;
          } else if (newPos >= 290) {
            newPos = 280;
          }
        }

        if (latOrLon) {
          $model3d.current.rotateCamera(activeLon, newPos);
        } else {
          $model3d.current.rotateCamera(newPos, activeLat);
        }

        const foo = latOrLon ? setActiveLat : setActiveLon;
        foo(newPos);
      };

      if (activeViewIndex === 0) {
        if (index === 1) {
          rotateCameraHelper(true, 40);
        }
        if (index === -1) {
          rotateCameraHelper(true, -40);
        }
        if (index === 2) {
          rotateCameraHelper(false, 40);
        }
        if (index === -2) {
          rotateCameraHelper(false, -40);
        }
      }
      //panorama controls
      else {
        if (index === 1) {
          setActiveYaw(activeYaw + 20);
        }
        if (index === -1) {
          setActiveYaw(activeYaw - 20);
        }
        if (index === 2) {
          setActivePtch(activePitch + 20);
        }
        if (index === -2) {
          setActivePtch(activePitch - 20);
        }
      }
    },
    [
      activeViewIndex,
      activePitch,
      activeYaw,
      activeLat,
      activeLon,
      setActivePtch,
      setActiveYaw,
      setActiveLat,
      setActiveLon,
    ]
  );

  //hotspottab toggle: focus on hotspot group when closed, focus on hotspottab content when open
  const handleTabToggle = useCallback(() => {
    if (tabToggle) {
      dispatch(setActiveHotspotIndex(null));
      hotspotGroupRef.current.focus();
    } else {
      hotspotTabContentGroupRef.current.focus();
    }
    setTabToggle(!tabToggle);
  }, [dispatch, tabToggle]);

  //HANDLE arrow control and FOCUS ON HOTSPOTS
  const handleKeyDownForHotspotFocus = useCallback(
    (e) => {
      if (e.keyCode === ENTER) {
        handleClickHotspot(hotspotFocusedIndex);
      }
      if (e.keyCode === UP) {
        if (hotspotFocusedIndex === 0) {
          // setHotspotFocusedIndex(activeView.hotspots.length - 1); //loop
        } else {
          setHotspotFocusedIndex(hotspotFocusedIndex - 1);
        }
      }
      if (e.keyCode === DOWN) {
        if (hotspotFocusedIndex === activeView.hotspots.length - 1) {
          // setHotspotFocusedIndex(0); //loop
        } else {
          setHotspotFocusedIndex(hotspotFocusedIndex + 1);
        }
      }
      if (e.keyCode === TAB) {
        e.preventDefault();
        if (e.shiftKey) {
          hotspotTabCloseButtonRef.current.focus();
        } else {
          arrowsContainerRef.current.focus();
        }
      }
    },
    [
      hotspotFocusedIndex,
      setHotspotFocusedIndex,
      activeView,
      handleClickHotspot,
    ]
  );

  // autofocus on hotspot when hotspotFocusedIndex changes
  useEffect(() => {
    if (hotspotFocusedIndex !== null && tooltipRef && tooltipRef.current) {
      //for panorama view
      if (activeViewIndex > 0) {
        setActivePtch(activeView.hotspots[hotspotFocusedIndex].location[0]);
        setActiveYaw(activeView.hotspots[hotspotFocusedIndex].location[1] - 10);
      }
      //for 3d model view
      else {
        setActiveLon(model.hotspots[hotspotFocusedIndex].loc[0]);
        setActiveLat(model.hotspots[hotspotFocusedIndex].loc[1]);
        $model3d.current.rotateCamera(
          model.hotspots[hotspotFocusedIndex].loc[0],
          model.hotspots[hotspotFocusedIndex].loc[1]
        );
      }
      setTimeout(() => {
        tooltipRef.current.focus();
      }, 500);
    }
  }, [hotspotFocusedIndex, activeViewIndex, model.hotspots, activeView]);

  const handleKeyDownHotspotGroupAD = (e) => {
    if (e.key === 'ArrowUp' || e.key === 'ArrowDown') {
      setHotspotFocusedIndex(0);
      setTimeout(() => {
        tooltipRef.current.focus();
        if (activeViewIndex > 0) {
          setActivePtch(views[activeViewIndex].panorama.pitch);
          setActiveYaw(views[activeViewIndex].panorama.yaw);
        } else {
          $model3d.current.rotateCamera(
            model.hotspots[0].loc[0],
            model.hotspots[0].loc[1]
          );
        }
      }, 100);
    } else {
      setHotspotFocusedIndex(null);
    }
  };

  //LISTENER FOR ARROW CONTROLS
  useArrowControls(
    isControlFocused,
    setControlFocused,
    isArrowControlActive,
    setArrowControlActive,
    handleArrows,
    arrowsContainerRef,
    setArrowPressedIndex
  );

  //Left/Right Arrows tabbing
  useLeftRightArrowTabbing(parentRef);

  return (
    <Root
      data-testid={`view-${activeViewIndex}`}
      refs={parentRef}
      activeScreenIndex={activeScreenIndex}
    >
      <AudioElement
        tabIndex={tabable(
          5,
          !tabToggle && !isTimeoutPopupActive && isActiveScreen
        )}
        aria-label={`${contentMatrix.main.tabGroup.hotspotGroupAD1} ${activeView.hotspots.length} ${contentMatrix.main.tabGroup.hotspotGroupAD2}`}
        aria-roledescription={' '}
        style={{ top: vw(100), right: vw(390), width: vw(1500), height: '85%' }}
        active={wasKeypadUsed}
        zindex={isHotspotGroupADFocusActive ? 1 : -1}
        ref={hotspotGroupRef}
        onKeyDown={handleKeyDownHotspotGroupAD}
        onFocus={() => {
          setControlFocused(false);
          setIsHotspotGroupADFocusActive(true);
        }}
        onBlur={() => {
          setIsHotspotGroupADFocusActive(false);
        }}
      ></AudioElement>
      <Panorama360
        settings={{
          pitch: activeView.panorama.pitch,
          yaw: activeView.panorama.yaw,
        }}
        image={activeView.panorama.image}
        hotspots={activeView.hotspots}
        activeHotspotIndex={activeHotspotIndex}
        onClickHotspot={handleClickHotspot}
        show={activeViewIndex !== 0}
        activePitch={activePitch}
        activeYaw={activeYaw}
        handleKeyDownForHotspotFocus={handleKeyDownForHotspotFocus}
        hotspotFocusedIndex={hotspotFocusedIndex}
        tooltipRef={activeViewIndex !== 0 ? tooltipRef : null}
      />

      <MainComponent show={activeViewIndex === 0}>
        <Model3D
          models={models}
          ref={$model3d}
          show={activeViewIndex === 0}
          hotspots={models[0].hotspots}
          hotspotStartTabIndex={hotspotStartTabIndex}
          activeHotspotIndex={activeHotspotIndex}
          onClickHotspot={handleClickHotspot}
          tooltipRef={activeViewIndex === 0 ? tooltipRef : null}
          handleKeyDownForHotspotFocus={handleKeyDownForHotspotFocus}
          hotspotFocusedIndex={hotspotFocusedIndex}
        />
      </MainComponent>

      <HotspotTab
        active={tabToggle && !isArrowControlActive && isActiveScreen}
        isActiveScreen={isActiveScreen}
        hotspot={
          activeHotspotIndex !== null
            ? activeView.hotspots[activeHotspotIndex]
            : {}
        }
        onClickCloseTab={handleTabToggle}
        onClickFirstHotspot={handleClickHotspot}
        onClickNext={handleClickNext}
        onClickPrevious={handleClickPrevious}
        activeView={activeView}
        activeHotspotIndex={activeHotspotIndex}
        hotspotTabContentGroupRef={hotspotTabContentGroupRef}
        hotspotTabCloseButtonRef={hotspotTabCloseButtonRef}
        handleArrowBoxBlur={() => setControlFocused(false)}
      />

      <AudioDescription
        description={contentMatrix.main.views[activeViewIndex].ad}
        active={!audioPlayed && activeScreenIndex === 2 && isActiveScreen}
        autoFocus={true}
        onBlur={handleAudioDescriptionBlur}
        zindex={'999'}
      />
      <StartOverOverlay />

      <Sidebar
        tabIndex={tabable(
          7,
          !isArrowControlActive && !isTimeoutPopupActive && isActiveScreen
        )}
        views={views}
        activeViewIndex={activeViewIndex}
        onClickTab={handleClickTab}
        isActiveHotspot={!tabToggle}
        handleArrowBoxBlur={() => setControlFocused(false)}
        arrowsContainerRef={arrowsContainerRef}
      />

      <StartOverButton
        tabIndex={tabable(
          8,
          !isArrowControlActive && !isTimeoutPopupActive && isActiveScreen
        )}
        zindex={'999'}
        startOverRef={startOverRef}
        nextItemRef={tabToggle ? hotspotTabContentGroupRef : hotspotGroupRef}
        sideTabRef={sideTabRef}
      />
      <ArrowControls
        active={false}
        handleArrows={(index) => handleArrows(index)}
        arrowsContainerRef={arrowsContainerRef}
        handleFocus={() => setControlFocused(true)}
        arrowPressedIndex={arrowPressedIndex}
        isArrowControlActive={isArrowControlActive}
        tabIndex={tabable(6, !isTimeoutPopupActive && isActiveScreen)}
      />
    </Root>
  );
}

const Root = styled(Page)`
  display: flex;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: 100%;
  background: black;
`;

const MainComponent = styled.div`
  display: ${(props) => (props.show ? 'block' : 'none')};
  position: absolute;
  width: 100%;
  height: 100%;
  left: 15%;
  top: 50%;
  transform: translate(-15%, -50%);
`;

const StartOverOverlay = styled.div`
  position: absolute;
  bottom: 0%;
  left: 0%;
  height: ${vw(116)};
  width: ${vw(236)};
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.67) 0%,
    rgba(0, 0, 0, 0.7) 100%
  );
`;

const AudioElement = styled.div`
  display: ${(props) => (props.active ? 'block' : 'none')};
  position: absolute;
  z-index: ${(props) => props.zindex};
  span {
    opacity: 0;
  }
`;
