import contentMatrix from '@/data/contentMatrix';

const views = contentMatrix.main.views;
// Constants
export const VIEWS__SET = 'VIEWS::SET';
export const ISSIDEBARUSED__SET = 'ISSIDEBARUSED::SET';
// Actions
export const setActiveView = (activeViewIndex) => {
  return {
    type: VIEWS__SET,
    payload: {
      activeViewIndex,
      activeView: views[activeViewIndex],
    },
  };
};
export const setIsSidebarUsed = (isSidebarUsed) => {
  return {
    type: ISSIDEBARUSED__SET,
    payload: {
      isSidebarUsed,
    },
  };
};

// Initial State
const initialState = {
  activeViewIndex: 0,
  activeView: views[0],
  isSidebarUsed: false,
};

// Reducer
const finalFoo = (state = initialState, action = {}) => {
  const { payload } = action;
  switch (action.type) {
    case VIEWS__SET:
      return {
        ...state,
        activeViewIndex: payload.activeViewIndex,
        activeView: payload.activeView,
      };
    case ISSIDEBARUSED__SET:
      return {
        ...state,
        isSidebarUsed: payload.isSidebarUsed,
      };

    default:
      return state;
  }
};

export default finalFoo;

export const activeViewIndexSelector = (state) => state.views.activeViewIndex;
