import React from 'react';
import styled, { keyframes } from 'styled-components';


import { vw } from '@/styles/utils';

export default function RingsDeco(props) {
  const imgColor = props.type ? `-${props.type}` : '';
  return (
    <Root {...props}>
      <Ring
        src={require(`@/assets/images/attract/ring-deco-1${imgColor}.png`)}
      />
      <Ring
        src={require(`@/assets/images/attract/ring-deco-2${imgColor}.png`)}
      />
      <Ring
        src={require(`@/assets/images/attract/ring-deco-3${imgColor}.png`)}
      />
    </Root>
  );
}

const animateRing = keyframes`
  100% {
    transform: rotate(360deg);
  }
`;

const Root = styled.div`
  width: ${vw(125)};
  height: ${vw(39)};
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Ring = styled.img`
  animation: ${animateRing} 5s linear infinite;
  &:nth-child(2) {
    animation-direction: reverse;
  }
`;
