import contentMatrix from '@/data/contentMatrix';

// Constants
export const SCREENS__SET = 'SCREENS::SET';
// Actions
export const setActiveScreenIndex = (activeScreenIndex) => {
  return {
    type: SCREENS__SET,
    payload: {
      activeScreenIndex,
    },
  };
};

// Initial State
const initialState = {
  activeScreenIndex: 0,
};

// Reducer
const finalFoo = (state = initialState, action = {}) => {
  const { payload } = action;
  switch (action.type) {
    case SCREENS__SET:
      return {
        ...state,
        activeScreenIndex: payload.activeScreenIndex,
      };

    default:
      return state;
  }
};

export default finalFoo;

export const activeScreenIndexSelector = (state) =>
  state.screens.activeScreenIndex;
