import React, { useCallback } from 'react';
import styled from 'styled-components';

const TAB = 9;
const ENTER = 13;

const DivWithRefControl = React.forwardRef(
  (
    {
      tabIndex,
      nextTabRef,
      prevTabRef,
      handleKeyDown,
      className,
      style,
      children = [],
      ...rest
    },
    ref
  ) => {

    const handleKeyboardInput = useCallback(
      (e) => {
        const { which } = e;
        switch (which) {
          case TAB:
            if (
              !e.shiftKey &&
              nextTabRef &&
              nextTabRef.current &&
              nextTabRef.current.focus
            ) {
              e.preventDefault();
              nextTabRef.current.focus();
            } else if (
              e.shiftKey &&
              prevTabRef &&
              prevTabRef.current &&
              prevTabRef.current.focus
            ) {
              e.preventDefault();
              prevTabRef.current.focus();
            }
            break;
            case ENTER: 
            handleKeyDown();
            break;
          default:
          //do nothing
        }
      },
      [ nextTabRef, prevTabRef, handleKeyDown,]
    );



    return (
      <Root
        ref={ref}
        tabIndex={tabIndex}
        className={className}
        style={style}
        onKeyDown={handleKeyboardInput}
        {...rest}
      >
          {React.Children.map(children, (child, index) => {
          return React.cloneElement(child, {
            tabIndex: -1,
            index: index,

          });
        })}
      </Root>
    );
  }
);

const Root = styled.button`

`;
export default DivWithRefControl;
