import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { setWasKeypadUsed } from '@/redux/app';
import { useHistory } from 'react-router-dom';
import { resetAction } from '@/redux/reducers'
const LEFT = 37;
const RIGHT = 39;
const TAB = 9;

/**
 * Detects whether keypad was used.
 */
export default function useKeypadDetect() {
  const dispatch = useDispatch();
  const history = useHistory();
  useEffect(() => {
    const handleKeyDown = (e) => {
      const which = e.keyCode;
      
        if(e.keyCode !==126 && e.keyCode !==75){
          dispatch(setWasKeypadUsed(true));
        }else{
          if(history.location.pathname ==='/' && e.keyCode === 126){
            history.push('/');
          }
          if(e.keyCode ===75){dispatch(resetAction())}
          dispatch(setWasKeypadUsed(false));
        }
         
    
      
      switch(which){
        case TAB:
            if(e.shiftKey){
                // e.preventDefault();
              }
              else{
                // e.preventDefault();
              }
          break;  
        case LEFT:
          //
          break;
        case RIGHT:
          //
          break;
        default:
          return;
      }
      
    };
    document.addEventListener('keydown', handleKeyDown);
    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [dispatch])
}
