import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { HOTSPOT_ACTIVE_BG, HOTSPOT_BG  } from '@/config/config';
import { loader } from '@/plugins/components/Preloader';
import { vw } from '@/styles/utils';

export default function Tooltip(props) {
  let current_bg = props.active
    ? loader.get(HOTSPOT_ACTIVE_BG).src
    : loader.get(HOTSPOT_BG).src;
  return (
    <Root
      {...props}
      ref={props.forwardRef}
      // ref={props.secondRef}
      style={{ backgroundImage: `url(${current_bg})` }}
      tabIndex={props.tabIndex}
      onClick={props.handleClick}
      onKeyDown={props.handlekeyDown}
    ></Root>
  );
}

Tooltip.propTypes = {
  active: PropTypes.bool,
  show: PropTypes.bool,
  forwardRef: PropTypes.object,
  hotspot: PropTypes.object,
};

Tooltip.defaultProps = {
  active: false,
  show: false,
  forwardRef: {},
  hotspot: {},
};

const Root = styled.div`

  display: flex;
  align-items: center;
  width: ${vw(420)};
  height: ${vw(120)};
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-11%, -50%) ;
  transition: all 0.25s;
  opacity: ${(props) => (props.show ? 1 : 0)}; 
  background-size: cover;
  background-repeat: no-repeat;
  
  h2 {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: ${vw(250)};
    height: ${vw(62)};
    top: 50%;
    transform: translate(55%, -50%);
    /* padding-top: ${vw(37)}; */
    /* background: green; */
    color: ${(props) =>
      props.active ? 'black' : ({ theme }) => theme.color.tertiary};
    font-family: ${({ theme }) => theme.font.nunito};
    font-size: ${vw(22)};
    line-height: ${vw(30)};
    font-weight: 600;
  }
`;
