import React, { useState } from 'react';
import styled from 'styled-components';
import { vw } from '@/styles/utils';
import { loader } from '@/plugins/components/Preloader';
import { HOTSPOT_ICON_ACTIVE, HOTSPOT_ICON, HOTSPOT_ACTIVE_BG, HOTSPOT_BG  } from '@/config/config';


export default function Hotspot({
  position,
  show,
  name,
  onClick,
  index,
  hotspotNums, 
  active,
  hide,
  handleKeyDownForHotspotFocus,
  tooltipRef,

}) {
  const [activeHotspotTabIndex, setTabIndex] = useState(-1);
 
  return (
    <Root
 
      style={{
        transform: `translate(-50%, -50%) translate(${position.x}px,${position.y}px)`,
      }}
      show={show}
      tabIndex={-1}
    >
      
     <HotspotBackground
        index={index}
        activeViewIndex={0}
        active={active}
        src={hide ? loader.get(HOTSPOT_BG).src : (active ? loader.get(HOTSPOT_ACTIVE_BG).src : null)}
        ref={tooltipRef}
        onClick={onClick}
        onFocus={()=>{setTabIndex(4)}}
        onBlur={()=>{setTabIndex(-1)}}
        onKeyDown={handleKeyDownForHotspotFocus}
        tabIndex={activeHotspotTabIndex}
        aria-label={`${index+1} of ${hotspotNums}, ${name}, ${active ? 'selected' : ''}`}
        aria-roledescription={' '}
     >
      <Dot show={show}
         src={active ? loader.get(HOTSPOT_ICON_ACTIVE).src : loader.get(HOTSPOT_ICON).src}
      />

      <Text 
        active={active} 
        hide={hide}    
        index={index}
      >
        <span>{name}</span>
      </Text>
     </HotspotBackground>
     
    </Root>
  );
}

const Root = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: ${vw(330)};
  height: ${vw(100)};
  opacity: ${(props) => (props.show ? 1 : 0)};
  transition: opacity 0.3s;
  background-size: contain;
  background-repeat: no-repeat;
  /* background: green; */
`;

const HotspotBackground = styled.div`
 position: absolute;
  top: 0;
  left: 50%;
  min-width: 100%;
  height: ${vw(100)};
  transition: opacity 0.3s;
  background-image: url(${props =>props.src});
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
`;

const Dot = styled.div`
  position: absolute;
  top: 50%;
  left: 17%;
  width: ${vw(54)};
  height: ${vw(54)};
  /* background: green; */
  transform: translate(-30%, -50%)
    ${(props) => (props.show ? 'scale(1)' : 'scale(0)')};
  transition: 0.3s;
  border-radius: 50%;
  background-image: url(${props=>props.src});
  background-size: contain;
`;

const Text = styled.div`
  position: absolute;
  opacity: ${(props) => (props.hide || props.active ? 1 : 0)};
  display: flex;
  align-items: center;
  justify-content: center;
  width: ${vw(200)};
  height: ${vw(50)};
  /* background: green; */
  top: 50%;
  left: ${props=>props.index===3 ? '55%' : props.index===6 ? '60%' : '65%'};
  transform: translate(-50%, -50%);
  transition: all 0.25s;
  pointer-events: ${(props) => (props.active ? 'auto' : 'none')};
  outline: none;
  color: ${(props) => (props.active ? 'black' : 'white')};
  /* background: red; */
 // 7 five windows 0 eva hand, else 65

  span {
    font-size: ${vw(20)};
    font-family: ${({ theme }) => theme.font.nunito};
    font-weight: 600;
    text-transform: capitalize;
   
  }
`;



